import React, { Component } from 'react';
import card_back from '../img/card_back.jpg';

class Collection extends Component {
  state = {
    userCards: null,
    viewedCard: ''
  }

  componentDidMount() {

    // load localStorage to state
    let users;
    if (!localStorage.getItem('users') || localStorage.getItem('users').length === 0) {
      users = [];
    } else {
      users = JSON.parse(localStorage.getItem('users'));
      for (let i = 0; i < users.length; i++) {
        if (users[i].loggedIn === true) {
          this.setState({
            userCards: users[i].collection,
          })
        }
      }
      console.log(this.state)
    }
  }

  // get card from api and add it to users collection
  addCard = e => {
    e.preventDefault();
    let name = document.querySelector('#collection__input').value;
    let query = 'https://api.scryfall.com/cards/named?fuzzy=' + name;

    fetch(query)
      .then(response => response.json())
      .then(card => {
        if (card.object === "error") {
          return null
        } else {
          let newCards = this.state.userCards;

          // check for doubles              -----------------------------TODO:     bierze pod uwagę tylko pierwszą z góry kartę
          if (newCards.length === 0) {
            newCards.push(card);
          } else {
            let end = false;
            for (let i = 0; i < newCards.length; i++) {
              console.log(i)
              if (card.name === newCards[i].name) {
                console.log('jest już taka karta')
                if (!newCards[i].count) {
                  newCards[i].count = 2;
                  console.log('nie ma counta')   
                  break;  
                } else {
                  newCards[i].count++;
                  console.log('jest count więc dodajemy')
                  break;
                }
              } else if ( i === newCards.length-1 && card.name != newCards[i].name){
                end = true
                console.log('nie ma takiej karty!', newCards)
              }
              // else {
              //   endTheLoop = true;                            // tu się psuje
              //   console.log('nie ma takiej karty',newCards)
                
              // }
            }
            if(end === true){

              newCards.unshift(card);
            }
          }

          // throw updated collection to state
          this.setState({
            userCards: newCards,
          })

          // throw state to local storage
          let users;
          if (!localStorage.getItem('users') || localStorage.getItem('users').length === 0) {
            users = [];
          } else {
            users = JSON.parse(localStorage.getItem('users'));
            for (let i = 0; i < users.length; i++) {
              if (users[i].loggedIn === true) {
                users[i].collection = this.state.userCards;
              }
            }
            localStorage.setItem('users', JSON.stringify(users))
          }
        }
      })
      .catch(error => {
        console.log(error);
      })
  }

  // show details on hovering over list items
  showDetails = e => {
    let name = e.target.innerText;
    let cards = this.state.userCards;

    for (let i = 0; i < cards.length; i++) {
      if (cards[i].name === name) {
        this.setState({
          viewedCard: cards[i],
        })
        break;
      }
    }
  }

  // if card has multiple faces - flip faces on click
  flipCardFace = (e) => {
    if (e.target.src === this.state.viewedCard.card_faces[0].image_uris.normal) {
      e.target.src = this.state.viewedCard.card_faces[1].image_uris.normal;
    } else {
      e.target.src = this.state.viewedCard.card_faces[0].image_uris.normal;
    }
  }

  render() {

    // card setup
    let card = this.state.viewedCard;
    let content;
    if (this.state.userCards === null) {
      content = <p>Add some cards</p>
    } else {
      content = this.state.userCards.map((card, index) => {
        // set styles accordingly...
        let colorId = card.color_identity;
        let rarity = card.rarity;
        let classes = [];

        //assign color classes
        if (colorId.length === 0) {
          classes.push('card-neutral');
        } else if (colorId.length === 1) {
          classes.push(colorId);
        } else if (colorId.length === 2) {
          colorId.forEach(color => classes.push(color));
          classes = classes.join('');
        } else {
          classes = 'multicolored';
        }

        return <li key={index} onMouseOver={this.showDetails} className={`collection__list-item ${classes}`} ><span className={rarity}>{card.name}</span><span className="card-count">{card.count ? card.count : null}</span></li>
      })
    }
    // image src setup
    let imgsrc;
    if (!card) {
      imgsrc = card_back;
    } else if (card.card_faces) {
      imgsrc = card.card_faces[0].image_uris.normal;
    } else {
      imgsrc = card.image_uris.normal;
    }

    return (
      <div className="collection__container">

        <form className="collection__input-area" onSubmit={this.addCard}>
          <label>
            <p>Write card name:</p>
            <input type="text" id="collection__input"></input>
          </label>
          <input type="submit" className="collection__btn" value="Add"></input>
        </form>

        <ul className="collection__list">
          {content}
        </ul>
        <div className="collection__card-details">
          <img src={imgsrc} alt={card === '' ? null : card.name} className="collection__image" onClick={card.card_faces ? this.flipCardFace : null}></img>
          <table className="collection__card-stats">
            <tbody>
              <tr>
                <th span="row">Name</th>
                <td>{card.name}</td>
              </tr>
              <tr>
                <th span="row">Type</th>
                <td>{card.type_line}</td>
              </tr>
              <tr>
                <th span="row">Colors</th>
                <td>{card.colors}</td>
              </tr>
              <tr>
                <th span="row">Color Identity</th>
                <td>{card.color_identity}</td>
              </tr>
              <tr>
                <th span="row">Mana Cost</th>
                <td>{card.mana_cost}</td>
              </tr>
              <tr>
                <th span="row">Rarity</th>
                <td>{card.rarity}</td>
              </tr>
              <tr>
                <th span="row">Release Date</th>
                <td>{card.released_at}</td>
              </tr>
              <tr>
                <th span="row">Set</th>
                <td>{card.set_name}</td>
              </tr>
              <tr>
                <th span="row">EDHREC Rank</th>
                <td>{card.edhrec_rank}</td>
              </tr>
              <tr>
                <th span="row">Price</th>
                <td>{card.prices ? card.prices.eur : null} €</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    )
  }
}

export default Collection;