import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import CardBrowser from './cardBrowser';
import Decks from './decks';
import User from './user';
import Welcome from './welcome-screen';
import SignUp from './welcome-screen/signup';
import SignIn from './welcome-screen/signin';
import Collection from './collection';
import {
  HashRouter,
  Route,
  Link,
  Switch,
  NavLink,
} from 'react-router-dom';


class Header extends Component {
  state = {
    loggedUser: '',
  }

  componentDidMount(){
    let users, currentUser;
    if (!localStorage.getItem('users') || localStorage.getItem('users').length === 0){
      users = [];
    } else {
      users = JSON.parse(localStorage.getItem('users'));
      for(let i=0; i<users.length; i++){
        if(users[i].loggedIn === true){
          currentUser = users[i].name;
        }
      }
      this.setState({
        loggedUser: currentUser,
      })
    }
  }

  render() {
    return (
      <header>
        <div id="logo">
          <h1>decks</h1>
        </div>
        <nav>
          <ul id="navigation">
            <li><NavLink exact to="/" className="menu-link" activeClassName="currentPage">start</NavLink></li>
            <li><NavLink to="/collection" className="menu-link" activeClassName="currentPage">collection</NavLink></li>
            <li><NavLink to="/decks" className="menu-link" activeClassName="currentPage">decks</NavLink></li>
            <li><NavLink to="/browse_cards" className="menu-link" activeClassName="currentPage">all cards</NavLink></li>
          </ul>
        </nav>
        <div id="user_display">
          <div id="user_display_options">
            <p id="user_display_name">{this.state.loggedUser}</p>
            <p id="user_display_show_navlinks"><span><NavLink to="/register" className="register_or_login">Sign In</NavLink></span> | <span><NavLink to="/login" className="register_or_login">Log In</NavLink></span></p>
          </div>
          <div id="user_avatar"></div>
        </div>
      </header>
    )
  }
}

class Footer extends Component {
  render() {
    return <footer></footer>
  }
}

class Skeleton extends Component {
  render() {
    return (
      <>
        <HashRouter>
          <Header></Header>
          <main>
            <Switch>
              <Route exact path="/" component={Welcome}></Route>
              <Route path="/register" component={SignUp}></Route>
              <Route path="/login" component={SignIn}></Route>
              <Route path="/collection" component={Collection}></Route>
              <Route path="/decks" component={Decks}></Route>
              <Route path="/browse_cards" component={CardBrowser}></Route>
            </Switch>
          </main>
        </HashRouter>
        <Footer></Footer>
      </>
    )
  }
}

export default Skeleton;