import React, { Component } from 'react';



class ManaIcon extends Component {
  constructor(props) {
    super(props)
  }
  handleFunction = (e) => {
    if (typeof this.props.clicked === 'function') {
      this.props.clicked(e);
    }
  }
  render() {
    return (
      <i className={this.props.classes} id={this.props.manacolor} onClick={this.handleFunction}></i>
    )
  }
}


class SearchAcard extends Component {
  constructor(props) {
    super(props)
  }
  handleFunction = (e) => {
    if (typeof this.props.onWrite === 'function') {
      this.props.onWrite(e);
    }
  }
  render() {
    return <input type="text" onChange={this.handleFunction}></input>
  }
}



class CardBrowser extends Component {

  state = {
    url: ' https://api.scryfall.com/cards/',
    currentQuery: '',
    // currentPage: 1,
    // currentPic: '',
    // searchFilters: {
    //   name: '',
    //   color: [],
    //   cost: null,
    // },
    cards: [],
  }

  handleColorsFilter = (e) => {
    e.target.classList.toggle('mana-query-on');
    let manaIcons = document.querySelectorAll('i');
    let colorsFiltered = [];
    let manaQuery = '';
    for (let i = 0; i < manaIcons.length; i++) {
      if (manaIcons[i].classList.contains('mana-query-on')) {
        colorsFiltered.push(manaIcons[i].id)
      }
    }
    manaQuery = colorsFiltered.join('');
    let newQuery = `${this.state.url}search?q=c%3A${manaQuery}`
    this.getCards(newQuery);
  }

  handleNameQuery = (e) => {
    let wantedName = `%2F${e.target.value}%2F/i`;
    let query = 'search?q=name%3A'
    query = this.state.url + query + wantedName;
    this.getCards(query);
  }

  /*
  search?q=
  c%3A{color}
  +
  cmc%3d{cmc}

  named?fuzzy=aust+com
  */



  getPic = e => {
    let cardGroup = this.state.cards;
    for (let i = 0; i < cardGroup.length; i++) {
      if (e.target.id === cardGroup[i].oracle_id) {
        this.setState({
          currentPic: cardGroup[i].image_uris.normal,
        })
      }
    }
  }

  getCards = (url) => {
    fetch(url)
      .then(response => {
        return response.json()
      })
      .then(all => {
        let cardArr = []
        let APIcards = all.data;
        APIcards.forEach((card, index) => {
          // if (index === 0){
            cardArr.push(card);
          // } else if (index > 0) {
          //   if (APIcards[index].name !== APIcards[index - 1].name) {
          //     cardArr.push(card);
          //   }
          // }
        })
        this.setState({
          cards: cardArr
        })
        console.log(this.state)
      })
      .catch(error => {
        let errorObj = [{oracle_id:'error', name:'no such card'}];
        this.setState({
          cards: errorObj,
        })
      });
  }

  componentDidMount() {
    // this.getCards(this.state.url);
  }

  render() {
    return (
      <>
        <div className="cardBrowser__inputs">
          <label>
            <p>Search:</p>
            <SearchAcard onWrite={this.handleNameQuery}></SearchAcard>

          </label>
          <ManaIcon classes='ms ms-cost ms-r' manacolor='r' clicked={this.handleColorsFilter}></ManaIcon>
          <ManaIcon classes='ms ms-cost ms-g' manacolor='g' clicked={this.handleColorsFilter}></ManaIcon>
          <ManaIcon classes='ms ms-cost ms-w' manacolor='w' clicked={this.handleColorsFilter}></ManaIcon>
          <ManaIcon classes='ms ms-cost ms-u' manacolor='u' clicked={this.handleColorsFilter}></ManaIcon>
          <ManaIcon classes='ms ms-cost ms-b' manacolor='b' clicked={this.handleColorsFilter}></ManaIcon>
          <ManaIcon classes='ms ms-cost ms-c' manacolor='c' clicked={this.handleColorsFilter}></ManaIcon>
          <label>
            <p>Converted mana cost</p>
            <input type="number" min="0" onChange={this.handleFilters}></input>
          </label>
        </div>
        <div className="cardBrowser__output">
          {
            this.state.cards.map((card, index) => {
              return <img key={index} className="card-image" src={!card.image_uris ? null : card.image_uris.normal} id={card.oracle_id} name={card.name} title={card.name}></img>
            })
          }
        </div>
      </>
    )
  }
}
export default CardBrowser

