import React, { Component } from 'react';
import ReactDOM from 'react-dom';

class Welcome extends Component {
  render(){
    return(
      <>
      <h2>Welcome to MTG decks</h2>
      <p>Here you can store your Magic The Gathering cards in an online collection, create decks and analyze them.</p>
      <p>This is the final project for Coder's Lab bootcamp.</p>
      <p>It is built with React.js and Sass.</p>
      <p>It uses data from <a href="https://scryfall.com/">Scryfall API</a>.</p>
      <p>The site currently uses browser's local storage to store user data.</p>
      <br></br>
      <h3>Sign in to begin adding cards</h3>
      <br></br>
      <h3>TODO:</h3>
      <ol>
        <li>fix user display (shows after refreshing)</li>
        <li>logging and switching users</li>
        <li>collection filters and searching</li>
        <li>improve search in cardbrowser</li>
        <li>autocompletion</li>
        <li>deck building</li>
        <li>styles styles styles</li>
        <li>responsiveness</li>
      </ol>
      <br></br>
      <h3>Changelog:</h3>
      <ul>
        <li>You can now build your collecion</li>
        <li>Sign In works</li>
        <li>You can now browse all Magic The Gathering cards</li>
      </ul>
      </>
    )
  }
}

export default Welcome;