import React, { Component } from 'react';

class SignUp extends Component {
  constructor(props){
    super(props)
  }

  /* 
  TODO 
  jak wysłać state stąd do headera, żeby nazwa usera od razu się wyświetliła po zmianie?
  */

  state = {
    loginUser: '',
    loginPassword: '',
  }

  sendToState = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    })
  }

  login = (e) => {
    e.preventDefault();

    /* Validate the Form */
    if (this.state.loginUser.length < 3) {
      alert('Nickname should be at least 4 characters long');
      document.querySelector('#signupForm').reset();
    } else if (this.state.loginPassword.length < 5) {
      alert('Password should be at least 5 characters long');
      document.querySelector('#signupForm').reset();
    } else {

      let users;

      if (!localStorage.getItem('users') || localStorage.getItem('users').length === 0) {
        users = [];
      } else {
        users = JSON.parse(localStorage.getItem('users'));
        users.forEach(user => user.loggedIn = false);
        for(let i = 0; i<users.length;i++){
          if(users[i].name === this.state.loginUser){
            alert(`There already is a ${users[i].name} here`);
            document.querySelector('#signupForm').reset();
            return null;
          }
        }
      }

      let User = {
        name: this.state.loginUser,
        password: this.state.loginPassword,
        avatar: '',
        collection: [],
        decks: [],
        loggedIn: true,
      }

      users.push(User)
      localStorage.setItem('users', JSON.stringify(users));
      document.querySelector('#signupForm').reset();
      this.setState({
        loginUser: '',
        loginPassword: '',
      })
    }
  }

  render() {
    return (
      <form id="signupForm" onSubmit={this.login}>
        <input type="text" name="loginUser" onChange={this.sendToState}></input>
        <input type="password" name="loginPassword" onChange={this.sendToState}></input>
        <input type="submit" value="send"></input>
      </form>
    )
  }
}

export default SignUp;